.navbar {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 9vh;
  color: #353839;
  background: linear-gradient(30deg, #FAF9F6 20.3%,  #209B47 79.6%); /* Set the background color the linear color */
  font-weight: 500;
  display: flex;
  min-height: 0vh;
  overflow: hidden;
  transition: transform 0.3s ease-in-out; /* Transitions for sliding and background color animations */
  align-items: center;
  justify-content: space-between;
}

.slide-up {
  transform: translateY(-100%); /* Slide the navbar up and off the screen */
}
.visible {
  transform: translateY(0%); /* Slide the navbar down and on the screen */
}

.logo-container {
  display: flex;
  align-items: center;
  margin: 0;
  position: fixed;
  z-index: 8;
  padding: 15px;
}

.logo {
  height: 50px;
  width: auto;
  margin:0;
}

.logo-text {
  margin: 0;
  font-size: 2.5rem;
  color: #353839;
  text-decoration: none;
}

.desktop-menu-center {
  font-size: 1.5rem;
  display: flex;
  align-items: center; /* Vertically align menu items */
  gap: 50px; /* Add spacing between menu items */
  margin-left: 40%;

}
.desktop-menu-right {
  font-size: 1.5rem;
  display: flex;
  align-items: center; /* Vertically align menu items */
  gap: 20px; /* Add spacing between menu items */
  margin-right: 2%;
}
.desktop-link {
  display: block;
  font-weight: 400;
  text-decoration: none;
  color: white;
}
.desktop-link:hover {
  border-bottom: 1px solid white;
}
.login {
  font-weight: 400;
  text-decoration: none;
  color: white;
}
.login:hover {
  border-bottom: 1px solid white;
}
.signup {
  display: inline-block;
  padding: 6px 12px; /* Adjust padding as needed */
  border: 1px solid white; /* White outline */
  border-radius: 4px; /* Rounded corners */
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
  text-decoration: none;
  color: white;
}
.signup:hover {
  border: 1px solid white;
  background-color: white;
  color: #209B47;
}
.menu {
  position: fixed;
  top: 1rem;
  right: 1rem;
  display: none;
}
 @media only screen and (max-width: 1700px) {
  .desktop-menu-center {
    font-size: 1.3rem;
    gap: 25px; /* Add spacing between menu items */
    margin-left: 40%;
  }
  .login {
    font-size: 1.3rem;
  }
  .signup {
    font-size: 1.3rem;
  }
  .logo-text {
    font-size: 2.3rem;
  }
  .logo {
    height: 45px;
  }
} 
